import React, { useContext,useEffect,useRef,useState } from 'react';
import { Button } from '@material-ui/core';
import { NavHashLink as NavLink } from 'react-router-hash-link';
import { makeStyles } from '@material-ui/core/styles';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import './Landing.css';
import { ThemeContext } from '../../contexts/ThemeContext';
import { headerData } from '../../data/headerData';
import { socialsData } from '../../data/socialsData';
import ApprovalIcon from '@mui/icons-material/Approval';
import PhoneIcon from '@mui/icons-material/Phone';
import Typed from 'typed.js';

import {
    FaTwitter,
    FaLinkedin,
    FaGithub,
    FaYoutube,
    FaBlogger,
} from 'react-icons/fa';
import Modal from '../Popup/Modal';




function Landing() {

    const { theme, drawerOpen } = useContext(ThemeContext);

    const [modalOpen, setModalOpen] = useState(false);

    const [name , setName] = useState("");

    const [email , setEmail] = useState("");

    const [contactNo, setContactNo] = useState("");

    const [checkIn , setCheckIn] = useState("");

    const [checkOut , setCheckOut] = useState("")


    const openModal = () => {
    setModalOpen(true);
    };

    const closeModal = () => {
    setModalOpen(false);
    };

    const typedRef = useRef(null);

useEffect(() => {
    const options = {
      strings: ["Enjoy Divine Trip at Hotels at Rameswaram", 'Enjoy Family Trip at Hotels at Rameswaram'], // Array of strings to be typed
      typeSpeed: 80, // Typing speed in milliseconds
      backSpeed: 50, // Backspacing speed in milliseconds
      cursorChar: '',
      loop:true,
    };
  
    typedRef.current = new Typed('.typing-element', options);
  
    // Cleanup function
    return () => {
      typedRef.current.destroy(); // Destroy the Typed instance
    };
  }, []);

    const useStyles = makeStyles((t) => ({
        resumeBtn: {
            color: theme.secondary,
            borderRadius: '30px',
            textTransform: 'inherit',
            textDecoration: 'none',
            width: '150px',
            fontSize: '1rem',
            fontWeight: '500',
            height: '50px',
            fontFamily: 'var(--primaryFont)',
            border: `3px solid ${theme.secondary}`,
            transition: '100ms ease-out',
            '&:hover': {
                backgroundColor: theme.secondary,
                color: theme.primary,
            },
            [t.breakpoints.down('sm')]: {
                width: '180px',
            },
        },
        iconBtn: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            width: 40,
            height: 40,
            borderRadius: 50,
            border: `2px solid #fff`,
            color: "#fff",
            transition: 'all 0.2s',
            '&:hover': {
                backgroundColor: theme.secondary,
                color: theme.primary,
                transform: 'scale(1.1)',
                border: `2px solid ${theme.secondary}`,
            },
        },
        contactBtn: {
            backgroundColor: theme.secondary,
            color: theme.primary,
            borderRadius: '30px',
            textTransform: 'inherit',
            textDecoration: 'none',
            width: '150px',
            height: '50px',
            fontSize: '1rem',
            fontWeight: '500',
            fontFamily: 'var(--primaryFont)',
            border: `3px solid ${theme.secondary}`,
            transition: '100ms ease-out',
            '&:hover': {
                backgroundColor: theme.primary,
                color: theme.secondary,
                border: `3px solid ${theme.secondary}`,
            },
            [t.breakpoints.down('sm')]: {
                display: 'none',
            },
        },
        input: {
            border: `4px solid ${theme.primary80}`,
            backgroundColor: `${theme.secondary}`,
            color: `${theme.tertiary}`,
            fontFamily: 'var(--primaryFont)',
            fontWeight: 500,
            transition: 'border 0.2s ease-in-out',
            '&:focus': {
                border: `4px solid ${theme.primary600}`,
            },
        },
        label: {
            backgroundColor: `${theme.secondary}`,
            color: `${theme.primary}`,
            fontFamily: 'var(--primaryFont)',
            fontWeight: 600,
            fontSize: '0.9rem',
            padding: '0 5px',
            transform: 'translate(25px,50%)',
            display: 'inline-flex',
        },
    }));

    const classes = useStyles();

    return (
        <div className='landing'>
            <div className='landing--container'>
                <div
                    className='landing--container-left'
                    style={{ backgroundColor: theme.secondary }}
                >
                    <div className='lcl--content'>
                        {socialsData.linkedIn && (
                            <a
                                href={socialsData.linkedIn}
                                target='_blank'
                                rel='noreferrer'
                            >
                                <FaLinkedin
                                    className='landing--social'
                                    style={{ color: theme.primary }}
                                    aria-label='LinkedIn'
                                />
                            </a>
                        )}
                        {socialsData.github && (
                            <a
                                href={socialsData.github}
                                target='_blank'
                                rel='noreferrer'
                            >
                                <FaGithub
                                    className='landing--social'
                                    style={{ color: theme.primary }}
                                    aria-label='GitHub'
                                />
                            </a>
                        )}
                        {socialsData.twitter && (
                            <a
                                href={socialsData.twitter}
                                target='_blank'
                                rel='noreferrer'
                            >
                                <FaTwitter
                                    className='landing--social'
                                    style={{ color: theme.primary }}
                                    aria-label='Twitter'
                                />
                            </a>
                        )}
                        {socialsData.youtube && (
                            <a
                                href={socialsData.youtube}
                                target='_blank'
                                rel='noreferrer'
                            >
                                <FaYoutube
                                    className='landing--social'
                                    style={{ color: theme.primary }}
                                    aria-label='YouTube'
                                />
                            </a>
                        )}
                        {socialsData.blogger && (
                            <a
                                href={socialsData.blogger}
                                target='_blank'
                                rel='noreferrer'
                            >
                                <FaBlogger
                                    className='landing--social'
                                    style={{ color: theme.primary }}
                                    aria-label='Blogger'
                                />
                            </a>
                        )}
                    </div>
                </div>
                <img
                    src={headerData.image}
                    alt=''
                    className='landing--img'
                    style={{
                        opacity: `${drawerOpen ? '0' : '1'}`,
                        borderColor: theme.primary,
                    }}
                />
                <div
                    className='landing--container-right'
                    
                >
                    <div
                        className='lcr--content'
                        style={{ color: theme.tertiary }}
                    >
                        
                        <h1 className='typing-element'>{headerData.name}</h1>
                        <p>{headerData.desciption}</p>

                        <div className='lcr-buttonContainer'>
                          
                                    <Button className={classes.resumeBtn} startIcon={<ApprovalIcon />} onClick={openModal}>
                                        Book Now
                                    </Button>
                               
                          
                            <NavLink
                                to='/#contacts'
                                smooth={true}
                                spy='true'
                                duration={2000}
                            >
                                <Button className={classes.contactBtn} startIcon={<PhoneIcon />}>
                                    Contact
                                </Button>
                            </NavLink>

                        <Button className={classes.iconBtn}>
                           < PlayArrowIcon />
                        </Button>
                        </div>
                    </div>
                </div>
            </div>
            <Modal isOpen={modalOpen} onClose={closeModal}>
                                <label htmlFor='Name' className={classes.label}>Name</label>
                                <input
                                    placeholder='John Doe'
                                    value={name}
                                    onChange={(e) => setName(e.target.value)}
                                    type='text'
                                    name='Name'
                                    className={`form-input ${classes.input}`}
                                />
                                <label htmlFor='Email' className={classes.label}>Email</label>
                                <input
                                    placeholder='John Doe'
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}
                                    type='text'
                                    name='Email'
                                    className={`form-input ${classes.input}`}
                                />

                                <label htmlFor='Mobile' className={classes.label}>Mobile</label>
                                <input
                                    placeholder='John Doe'
                                    value={contactNo}
                                    onChange={(e) => setContactNo(e.target.value)}
                                    type='text'
                                    name='Mobile'
                                    className={`form-input ${classes.input}`}
                                />
                               <label htmlFor='Checkin' className={classes.label}>Check-In Date</label>
                                <input
                                    placeholder='John Doe'
                                    value={checkIn}
                                    onChange={(e) => setCheckIn(e.target.value)}
                                    type='date'
                                    name='Checkin'
                                    className={`form-input ${classes.input}`}
                                />
                                <label htmlFor='Checkout' className={classes.label}>Check-Out Date</label>
                                <input
                                    placeholder='John Doe'
                                    value={checkIn}
                                    onChange={(e) => setCheckOut(e.target.value)}
                                    type='date'
                                    name='Checkout'
                                    className={`form-input ${classes.input}`}
                                />
            </Modal>
        </div>
    );
}

export default Landing;
