import React, { useContext, useRef } from 'react';

import Slider from 'react-slick';

import { FaQuoteLeft, FaArrowRight, FaArrowLeft } from 'react-icons/fa';

import { ThemeContext } from '../../contexts/ThemeContext';
import { galleryData } from '../../data/galleryData';

import './Testimonials.css';
import { NavLink } from 'react-router-dom';
import ButtonContainer from '../Button/Button';

function Gallery() {
    const { theme } = useContext(ThemeContext);
    const sliderRef = useRef();

    const settings = {
        dots: true,
        adaptiveHeight: true,
        infinite: true,
        speed: 800,
        arrows: false,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: false,
        margin: 3,
        loop: true,
        autoplaySpeed: 3000,
        draggable: true,
        swipeToSlide: true,
        swipe: true,
    };

    const gotoNext = () => {
        sliderRef.current.slickNext();
    };

    const gotoPrev = () => {
        sliderRef.current.slickPrev();
    };

    return (
        <>
            
                <div
                    className='testimonials'
                    style={{backgroundColor:theme.secondary}}
                >
                    <div className='testimonials--header'>
                        <h1 style={{ color: theme.primary }}>Gallery</h1>
                    </div>
                    <div className='testimonials--body'>
                        <div
                            className='testimonials--slider'
                        >
                            <Slider {...settings} ref={sliderRef}>
                                {galleryData.slice(0,5).map((test) => (
                                    <div
                                        className='single--testimony'
                                        key={test.id}
                                    >
                                        <div className='testimonials--container'>
                                            <div
                                                className='gallery--content'
                                                style={{
                                                    backgroundColor:
                                                        theme.secondary,
                                                    color: theme.tertiary,
                                                }}
                                            >
                                                
                                                <img src={test.img} alt={test.id} />
                                               
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </Slider>
                            <button
                                className='prevBtn'
                                onClick={gotoPrev}
                                style={{ backgroundColor: "#ffff" }}
                            >
                                <FaArrowLeft
                                    style={{ color: theme.primary }}
                                    aria-label='Previous testimonial'
                                />
                            </button>
                            <button
                                className='nextBtn'
                                onClick={gotoNext}
                                style={{ backgroundColor: "#ffff" }}
                            >
                                <FaArrowRight
                                    style={{ color: theme.primary }}
                                    aria-label='Next testimonial'
                                />
                            </button>
                        </div>
                    </div>
                    <NavLink to="/blog" style={{margin:"2rem"}}>
                        <ButtonContainer title="View All" />
                    </NavLink>
                </div>
            
        </>
    );
}

export default Gallery;
