import React, { useContext } from 'react';

import { ThemeContext } from '../../contexts/ThemeContext';

import './Education.css'
import EducationCard from './EducationCard';

import { educationData } from '../../data/educationData'

function Education() {

    const { theme } = useContext(ThemeContext);
    return (
        <div className="education" id="resume" style={{backgroundColor: theme.secondary}}>
           
            <div className="education-body">
                <div className="education-description">
                <h1 style={{color:theme.primary}}>About Us</h1>
                    {educationData.map(edu => (
                        <EducationCard 
                            key={edu.id}
                            id={edu.id}
                            institution={edu.institution}

                        />
                    ))}
                </div>
                <div className="education-image">
                <lottie-player src="https://assets2.lottiefiles.com/packages/lf20_v1yudlrx.json"  background="transparent"  speed="1"  style={{width: "500px", height:"600px"}}  loop  autoplay></lottie-player>
                </div>
            </div>
        </div>
    )
}

export default Education
