import React, { useContext, useState } from 'react'
import { Helmet } from 'react-helmet'
import { Grid } from '@material-ui/core'
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';
import { AiOutlineHome } from "react-icons/ai";
import { RiGalleryFill } from 'react-icons/ri';

import './BlogPage.css'
import { SingleBlog } from '../../components'
import { ThemeContext } from '../../contexts/ThemeContext';
import { blogData } from '../../data/blogData'
import { headerData } from '../../data/headerData'

import Box from '@mui/material/Box';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import { galleryData } from '../../data/galleryData';
import Modal from '../../components/ImagePopup/Modal';

function BlogPage() {

    const [search, setSearch] = useState('');

    const [modalOpen, setModalOpen] = useState(false);

    const [selectedImage, setSelectedImage] = useState(null);

    const PopupOpen = (e) => {
        setModalOpen(true)
        const src = e.target.src;
        setSelectedImage(src)

    };

    const PopupClose = () => (setModalOpen(false));

    const { theme } = useContext(ThemeContext);

    const filteredArticles = blogData.filter((blog) => {
        const content = blog.title + blog.description + blog.date
        return content.toLowerCase().includes(search.toLowerCase())
    })


    const useStyles = makeStyles((t) => ({


        search: {
            color: theme.tertiary,
            width: '40%',
            height: '2.75rem',
            outline: 'none',
            border: 'none',
            borderRadius: '20px',
            padding: '0.95rem 1rem',
            fontFamily: "'Noto Sans TC', sans-serif",
            fontWeight: 500,
            fontSize: '0.9rem',
            backgroundColor: theme.secondary,
            boxShadow: theme.type === 'dark' ? 'inset 3px 3px 6px #ffffff10, inset -3px -3px 6px #00000060' : 'inset 3px 3px 6px #ffffffbd, inset -3px -3px 6px #00000030',
            "&::placeholder": {
                color: theme.tertiary80,
            },
            [t.breakpoints.down('sm')]: {
                width: '350px',
            },
        },
        home: {
            color: theme.secondary,
            position: 'absolute',
            top: 25,
            left: 25,
            padding: '7px',
            borderRadius: '50%',
            boxSizing: 'content-box',
            fontSize: '2rem',
            cursor: 'pointer',
            boxShadow: theme.type === 'dark' ? '3px 3px 6px #ffffff40, -3px -3px 6px #00000050' : '3px 3px 6px #ffffff40, -3px -3px 6px #00000050',
            transition: 'all 0.3s ease-in-out',
            "&:hover":
            {
                color: theme.tertiary,
                transform: 'scale(1.1)',
            },
            [t.breakpoints.down('sm')]: {
                fontSize: '1.8rem',
            },
        },
    }));

    const classes = useStyles();

    return (
        <div className="blogPage" style={{ backgroundColor: theme.secondary }}>
            <Helmet>
                <title>Seven Hills | Gallery</title>
            </Helmet>
            <div className="blogPage--header" style={{ backgroundColor: theme.primary }}>
                <Link to="/">
                    <AiOutlineHome className={classes.home} />
                </Link>

                <h2>Seven Hills</h2>

                <div className='gallery--header'>

                    <RiGalleryFill />
                    <h1 style={{ color: theme.secondary }}> Our Gallery</h1>
                </div>

            </div>
            <div className="blogPage--container">
                <div className="blogs--container">

                    <ImageList variant="quilted" cols={3} gap={8}>
                        {galleryData.map((item) => (
                            <>
                                <ImageListItem key={item.id}>
                                    <img
                                        src={`${item.img}?w=248&fit=crop&auto=format`}
                                        srcSet={`${item.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
                                        alt={item.title}
                                        loading="lazy"
                                        onClick={PopupOpen}
                                    />

                                </ImageListItem>

                            </>

                        ))}
                    </ImageList>

                    <Modal isOpen={modalOpen} onClose={PopupClose}>
                        <img src={selectedImage} alt="popup" width="100%" />
                    </Modal>


                </div>
            </div>
        </div>
    )
}

export default BlogPage
