import one from '../assets/img/one.jpg'
import two from '../assets/img/two.jpg'
import three from '../assets/img/three.jpg'
import four from '../assets/img/five.jpg'
import five from '../assets/img/four.jpg'



export const skillsImage = (skill) => {
    const skillID = skill.toLowerCase();
    switch (skill) {
        case 'Pamban Bridge':
            return one;
        case 'Dhanushkodi':
            return two;
        case 'Rameswaram Temple':
            return three;
        case 'Ramar Patham':
            return four;
        case 'Viloondi Theertham':
            return five;
       
        default:
            break;
    }
}
